.Contact{
    width: 100vw;
    font-family: 'Maven Pro', sans-serif;

}

.Name_Email{
    position: absolute;
    width: 35vw;
    left: 12vw;
    top: 31vh;
    background-color: rgb(14,14,27);
    font-size: 3vh;
}

.Name_Email h1{

    margin-bottom: 5vh;
    color: rgb(48,154,115);
    -webkit-animation: bounce-in-top 1.1s both;
    animation: bounce-in-top 1.1s both;

}

.Name_Email label{
    margin-right: 1vw;
    color: rgb(48,154,115);

}


input{
    font-size: 2vh;
    background-color: rgb(14,14,27);
    color: silver;
    border-width: 1px;
    border-top: none;
    border-left: none;
    border-right: none;
    /* border-color: silver; */
    border-color: rgb(255,118,21);

}


#Main_Form{
    display: flex;
    width: 100vw;
    height: 50vh;
}
.Name_Form{
    display: flex;
    width: 35vw;
}

.Email_Form{
    display: flex;
    width: 35vw; 
}
.Message_Form{
    position: absolute;
    left: 45vw;
    top: 28vh;
    height: 40vh;
    width: 40vw;
    

}
.Message_Form h2{
    position: absolute;
    z-index: 100;
    top: -2vh;
    color: rgb(48,154,115);
    -webkit-animation: bounce-in-top .5s both;
    animation: bounce-in-top .5s both;
}

input:focus,
select:focus,
textarea:focus,
button:focus {
    outline: none;
}


textarea {
    background-color:rgb(9,24,38);
    border: none;
    overflow: auto;
    outline: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    color: silver;
    caret-color: rgb(255, 118, 21);
    font-size: 2vh;
    padding: 1vw;
    -webkit-animation: bounce-in-top 1.5s both;
    animation: bounce-in-top 1.5s both;
}


#message.form-control{
    width: 40vw;
    height: 40vh;
}

#Submit_Btn {
        position: absolute;
        top: 64vh;
        left: 12vw;
        font-size: 3vh;
        list-style: none;
        color: rgb(48,154,115);
}
#Submit_Btn:hover{
        font-size: 3.3vh;
        cursor: pointer;
        color: rgb(255,118,21);
}

.Success{
    position: absolute;
    top: 55vh;
    left: -3vw;
    font-size: 1.5vh;
    color: rgb(48,154,115);
    margin-top: 17vh;
    margin-left: 48vw;
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

}
.Incomplete_Form{
    position: absolute;
    top: 57vh;
    left: -3vw;
    font-size: 1.5vh;
    color: rgb(255, 118, 21);
    margin-top: 17vh;
    margin-left: 48vw;
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
.Failure{
    position: absolute;
    top: 55vh;
    left: -3vw;
    font-size: 1.5vh;
    color: silver;
    margin-top: 17vh;
    margin-left: 48vw;
}
.Failure p{
    font-size: 2vh;
}
.Failure a{
    font-size: 2vh;
    cursor: pointer;
    color: rgb(255,118,21);
}
.Failure a:hover, a:focus a:active {
        font-size: 2.6vh;
        color: rgb(48,154,115);
    
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  /* border: 1px solid green; */
  -webkit-text-fill-color: silver;
  -webkit-box-shadow: 0 0 0px 1000px rgb(14,14,27) inset;
  transition: background-color 5000s ease-in-out 0s;
}

